/*--------------------------------------------------------------
# Footer
--------------------------------------------------------------*/
.footer {
    background:  rgb(180, 180, 180);
    padding: 0 0 10px 0;
    font-size: 14px;
  }
  
  
  
  
  .footer .footer-top {
    background: rgb(255, 251, 251) no-repeat right top;
    background-size: contain;
    border-top: 1px solid #ffffff;
    border-bottom: 1px solid #9e9696;
    padding: 60px 0 30px 0;
  }
  
  .footer .footer-top .footer-info {
    margin-bottom: 10px;
  }
  
  .footer .footer-top .footer-info .logo {
    line-height: 0;
    margin-bottom: 11px;
  }
  
  .footer .footer-top .footer-info .logo img {
    max-height: 30px;
    margin-right: 6px;
  }
  
  .footer .footer-top .footer-info .logo span {
    font-size: 30px;
    font-weight: 700;
    letter-spacing: 1px;
    color: #012970;
    font-family: "Nunito", sans-serif;
    margin-top: 2px;
  }
  
  .footer .footer-top .footer-info p {
    font-size: 14px;
    line-height: 24px;
    margin-bottom: 0;
    font-family: "Nunito", sans-serif;
  }
  
  .footer .footer-top .social-links a {
    font-size: 20px;
    display: inline-block;
    color: rgba(1, 41, 112, 0.5);
    line-height: 0;
    margin-right: 10px;
    transition: 0.3s;
  }
  
  .footer .footer-top .social-links a:hover {
    color: #012970;
  }
  
  .footer .footer-top h4 {
    font-size: 16px;
    font-weight: bold;
    color: #012970;
    text-transform: uppercase;
    position: relative;
    padding-bottom: 5px;
  }
  
  .footer .footer-top .footer-links {
    margin-bottom: 10px;
  }
  
  .footer .footer-top .footer-links ul {
    list-style: none;
    padding: 0;
    margin: 0;
  }
  
  .footer .footer-top .footer-links ul i {
    padding-right: 2px;
    color: #d0d4fc;
    font-size: 12px;
    line-height: 0;
  }
  
  .footer .footer-top .footer-links ul li {
    padding: 5px 0;
    display: flex;
    align-items: center;
  }
  
  .footer .footer-top .footer-links ul li:first-child {
    padding-top: 0;
  }
  
  .footer .footer-top .footer-links ul a {
    color: #013289;
    transition: 0.3s;
    display: inline-block;
    line-height: 1;
  }
  
  .footer .footer-top .footer-links ul a:hover {
    color: #4154f1;
  }
  
  .footer .footer-top .footer-contact p {
    line-height: 26px;
  }
  
  .footer .copyright {
    text-align: center;
    padding-top: 10px;
    color: #012970;
  }
  
  .footer .credits {
    text-align: center;
    font-size: 13px;
    color: #012970;
  }